const context = require('../../context/Java110Context.js');
const factory = context.factory;;
const constant = context.constant;
export default {
	data() {
		return {};
	},

	components: {},
	props: {},

	/**
	 * 生命周期函数--监听页面加载
	 */
	onLoad: function(options) {
		context.onLoad(options);
	},

	/**
	 * 生命周期函数--监听页面初次渲染完成
	 */
	onReady: function() {},

	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow: function() {},

	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {},

	/**
	 * 生命周期函数--监听页面卸载
	 */
	onUnload: function() {},

	/**
	 * 页面相关事件处理函数--监听用户下拉动作
	 */
	onPullDownRefresh: function() {},

	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {},

	/**
	 * 用户点击右上角分享
	 */
	onShareAppMessage: function() {},
	methods: {
		clickHerf: function(pagUrl) {
			if(pagUrl == 'noPage'){
				uni.showToast({
					icon:'none',
					title:'正在开发中'
				});
				return;
			}
			uni.navigateTo({
				url: '/pages/' + pagUrl
			});
		}
	}
};
